.ul_Perfil {
  margin-top: 5px;
  list-style: none;
  display: flex;
  justify-content: space-evenly;
  margin-left: 3vh;
}

.ul_Perfil li {
  color: #333;
  font-size: 14px;
  margin-top: 5px;
}

.iconCart {
  margin-top: 7px;
  cursor: pointer;
}

.iconMoon {
  margin-top: 7px;
  cursor: pointer;
}

@media (max-width: 1400px) {
  .ul_Perfil {
    display: flex;
  }
  .ul_Perfil li {
    display: none;
  }

  .iconCart {
    padding: 1vh;
    border-radius: 100%;
    background-color: rgb(246, 246, 127);
    box-shadow: 1px 1px 2px 1px rgb(193, 193, 31);
  }

  .iconMoon {
    padding: 1vh;
    border-radius: 100%;
    background-color: rgb(246, 246, 127);
    box-shadow: 1px 1px 2px 1px rgb(193, 193, 31);
    margin-left: 1vh;
    
  }

  .section_perfil {
    margin-left: 2vh;
  }
}




@media (max-width: 740px){
     .ul_Perfil{
      margin: 0;
      padding: 0;
      width: 30px;
      margin-left: 3px;


     }
     .iconMoon{
       display: none;
     }

     .iconCart{
      width: 25px;
      height: 25px;
      padding-top: 8px;
      padding-left: 3px; ;
      margin: 0;
      box-shadow:none;
      color: rgb(144, 144, 144);
      background-color: #fff159;
  
     }

}
